    (function cabinsPrivelegesMore() {
        const btn = document.querySelectorAll('.cabins-privilege__show');
        if (!btn) return;
        btn.forEach((c) => {
            const parent = c.closest('.cabins-privilege__more');
            if (!parent) return;
            c.addEventListener('click', (e) => {
                e.preventDefault();
                parent.classList.toggle('cabins-privilege__more--showed');
            });
        });
    })();

