    (function selectToggle() {
        const selectes = document.querySelectorAll('.select-filter__select-control, .select-filter__apply');

        for (let i = 0; i < selectes.length; i++) {
            const item = selectes[i];
            const parent = item.closest('.select-filter__select');
            item.addEventListener('click', () => {
                parent.classList.toggle('select-filter__select_open');
            });
        }
    })();

    (function selectRadioSet() {
        const radios = document.querySelectorAll('.select-filter__radio');

        for (let i = 0; i < radios.length; i++) {
            const item = radios[i];
            const parent = item.closest('.select-filter__select');
            const control = parent.querySelector('.select-filter__select-control');
            const placeholder = control.dataset.placeholder || "Выберите";
            item.addEventListener('click', () => {
                parent.classList.toggle('select-filter__select_open');
                control.textContent = item.dataset.value || item.value;
                control.classList.add('select-filter__select-control_value');
            });
        }
    })();

    document.addEventListener('click', function (event) {
        const items = document.querySelectorAll('.select-filter__select');
        let result = false;
        for (let i = 0; i < items.length; i++) {
            let isClickInsideElement = items[i].contains(event.target);
            if (isClickInsideElement) {
                result = true;
                break;
            }
        }
        if (!result) {
            for (let i = 0; i < items.length; i++) {
                items[i].classList.remove('select-filter__select_open');
            }
        }
    });

